  .button {
    display: inline-block;
    color: #fff;
    text-transform: uppercase;
    font-size: 1rem;
    letter-spacing: .15rem;
    transition: all .3s;
    position: relative;
    overflow: hidden;
    z-index: 1;
  }
  
  .button::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(74, 76, 77);
    z-index: -2;
  }
  
  .button::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0%;
    height: 100%;
    background-color: rgba(23, 24, 24, 0.867);
    transition: all .3s;
    z-index: -1;
  }
  
  .button:hover {
    color: #fff;
  }
  
  .button:hover::before {
    width: 100%;
  }
  